import React, { useContext } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import MenuItem from '@material-ui/core/MenuItem';
import { IconButton, Menu } from '@material-ui/core';
import LanguageIcon from '@material-ui/icons/Language';

import LocaleContext from './LocaleContext';
import i18n from './i18n/i18n';

const useStyles = makeStyles((theme) => ({
  formControl: {
    backgroundColor: 'white',
    margin: theme.spacing(2),
    minWidth: 60,
  },
  langSwitch: {
      fontSize: '0.75rem',
      padding: '0.5rem'
  }
}));

export default function LanguageSwitch(props: any) {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);
  const localeCtx = useContext(LocaleContext);
  const {browserLocale, setBrowserLocale} = localeCtx;

  const handleChange = (lang: string) => {
    i18n.changeLanguage(lang);
    setBrowserLocale(lang);
    setAnchorEl(null);
  };


  const handleClose = (action: string) => {
    setAnchorEl(null);
  };

  
  const handleMenu = (event: React.MouseEvent<HTMLElement>, step: number) => {
    setAnchorEl(event.currentTarget);
  };

  return (
    <React.Fragment>
    <IconButton
      aria-label="account of current user"
      aria-controls="menu-appbar"
      aria-haspopup="true"
      // onClick={handleMenu}
      onClick={(e) => handleMenu(e,3)}
      color="inherit"
    >
      <LanguageIcon />
      <span className={classes.langSwitch}>{browserLocale.toUpperCase()}</span>
    </IconButton>
    <Menu
      id="menu-appbar"
      anchorEl={anchorEl}
      anchorOrigin={{
        vertical: 'top',
        horizontal: 'right',
      }}
      keepMounted
      transformOrigin={{
        vertical: 'top',
        horizontal: 'right',
      }}
      open={open}
      onClose={handleClose}
      style = {{ zIndex: 999999 }}
    >
      <MenuItem onClick={() => handleChange('en')}>EN</MenuItem>
      <MenuItem onClick={() => handleChange('de')}>DE</MenuItem>
    </Menu>
  </React.Fragment>
  );
}
