import React, { useContext, useEffect} from 'react'
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress';
import { makeStyles } from '@material-ui/core/styles';
import LocaleContext from "./LocaleContext";
import { useNavigate  } from 'react-router-dom';
import axios from 'axios';

const useStyles = makeStyles((theme) => ({
    backdrop: {
      zIndex: theme.zIndex.drawer + 1,
      color: '#fff',
    },
  }));

function AuthComponent() {
    const classes = useStyles();
    const navigate = useNavigate();
    const globalCtx = useContext(LocaleContext);
    const {authenticated, setAuthenticated} = globalCtx;


    const getUrlVars = () => {
        const vars: any = {};
        const regex = /[?&]+([^=&]+)=([^&]*)/gi; // Must be declared outside the while expression,
        // and must include the global "g" flag.
      
        let result;
        while ((result = regex.exec(window.location.href))) {
          vars[result[1]] = result[2];
        }
        return vars;
    };


    function redirectToHome(){
        navigate('/')
    }

    function ssoLogin() {
        console.log('getUrlVars():'+getUrlVars()['code']);
        if (getUrlVars()['code'] === undefined) {
            window.location.href = "https://sso-int.mercedes-benz.com/as/authorization.oauth2?response_type=code&client_id=60f0f6a2-46d5-4697-bf71-46db274c317b&scope=openid&redirect_uri=https://pages.git.daimler.com/itcve/vedbs-admin-panel/&state=VEDB"
        }else{
            console.log('code generated');
            console.log('getUrlVars():'+getUrlVars()['code']);
            const authCode = getUrlVars()['code'];
            sessionStorage.setItem('token',  authCode);
            localStorage.setItem('token',  authCode);
            alert(authCode);
            axios({
                method: 'get',
                url: 'https://sma4u-ti2.es.corpintra.net/sf/vedbs/projects',
                headers: { 
                  'Authorization': 'Bearer '+authCode, 
                }
            })
            .then((response:any)=>{
                console.log(response);
            },(error: any)=>{
                console.log(error);
            })
            setAuthenticated(true);
        }
    }
    

    useEffect(() => {
        if (authenticated) {
            alert('redirecting');
            redirectToHome()
        }else{
            ssoLogin();
        }
    }, [authenticated])

  return (
      <Backdrop className={classes.backdrop} open={!authenticated} >
          <CircularProgress color="inherit" />
      </Backdrop>
  )
}

export default AuthComponent;