import * as React from "react";
import { Routes, Route } from "react-router-dom";
import AdminPanel from "./AdminPanel";

import Dashboard from "./Dashboard";
import Header from "./Header";
import { DUITheme } from '@daimler/material-ui-theme';
import { MuiThemeProvider, CssBaseline } from '@material-ui/core';
import i18n from "./i18n/i18n";
import LocaleContext from "./LocaleContext";
import AuthComponent from "./AuthComponent";



export default function App() {

  const [authenticated, setAuthenticated] = React.useState(false);
  const [browserLocale, setBrowserLocale] = React.useState(i18n.language);

  return (
    <div className="App">
      <MuiThemeProvider theme={DUITheme}>
        <CssBaseline>
          <LocaleContext.Provider value={{browserLocale, setBrowserLocale, authenticated, setAuthenticated}}>
          <React.Suspense fallback="Loading..">
              <Header />
              <Routes>
                <Route path="/" element={authenticated ? <Dashboard /> : <AuthComponent />} />
                <Route path="/admin" element={authenticated ? <AdminPanel /> : <AuthComponent />} />
                <Route path="/auth" element={<AuthComponent />} />
              </Routes>
            </React.Suspense>
          </LocaleContext.Provider>
        </CssBaseline>
      </MuiThemeProvider>
    </div>
  );
}